"use client";

import { useState, useEffect, useRef, useCallback } from 'react';
import { ArrowDown, ArrowUp, TrendingUp, Sun, Moon } from 'lucide-react';
import PriceCard from '@/components/price-card';
import RatioCard from '@/components/ratio-card';
import ChangeLogTable from '@/components/change-log-table';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';

export type PriceData = {
  price: number;
  direction: 'up' | 'down' | 'none';
};

export type RatioChange = {
  timestamp: string;
  ratio: number;
  change: number;
};

export default function Home() {
  const [walPrice, setWalPrice] = useState<PriceData>({ price: 0.05123, direction: 'none' });
  const [suiPrice, setSuiPrice] = useState<PriceData>({ price: 1.0234, direction: 'none' });
  const [ratio, setRatio] = useState<number>(0);
  const [ratioChanges, setRatioChanges] = useState<RatioChange[]>([]);
  const [lastRatio, setLastRatio] = useState<number | null>(null);
  const [theme, setTheme] = useState('light');

  const previousRatioRef = useRef<number | null>(null);

  const toggleTheme = () => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    setTheme(newTheme);
    document.documentElement.classList.toggle('dark', newTheme === 'dark');
  };

  // Simulate price updates
  useEffect(() => {
    const priceUpdateInterval = setInterval(() => {
      setWalPrice(prev => {
        const changePercent = (Math.random() - 0.5) * 0.05; // -2.5% to +2.5% change
        const newPrice = prev.price * (1 + changePercent);
        return {
            price: newPrice,
            direction: newPrice > prev.price ? 'up' : 'down'
        };
      });
      
      setSuiPrice(prev => {
        const changePercent = (Math.random() - 0.5) * 0.02; // -1% to +1% change
        const newPrice = prev.price * (1 + changePercent);
        return {
            price: newPrice,
            direction: newPrice > prev.price ? 'up' : 'down'
        };
      });
    }, 1000);

    return () => clearInterval(priceUpdateInterval);
  }, []);

  // Calculate ratio and log changes
  useEffect(() => {
    if (suiPrice.price > 0) {
      const newRatio = walPrice.price / suiPrice.price;
      setRatio(newRatio);

      if (previousRatioRef.current === null) {
        previousRatioRef.current = newRatio;
      } else {
        const oldRatioFixed = previousRatioRef.current.toFixed(6);
        const newRatioFixed = newRatio.toFixed(6);

        if (oldRatioFixed !== newRatioFixed) {
          const change = ((newRatio - previousRatioRef.current) / previousRatioRef.current) * 100;
          const newChange: RatioChange = {
              timestamp: new Date().toLocaleTimeString(),
              ratio: newRatio,
              change: change
          };
          setRatioChanges(prev => [newChange, ...prev].slice(0, 10));
          previousRatioRef.current = newRatio;
        }
      }
    }
  }, [walPrice, suiPrice]);

  useEffect(() => {
    if (ratio !== 0) {
      if (lastRatio === null) {
        setLastRatio(ratio);
      }
    }
  }, [ratio, lastRatio]);
  
  const ratioDirection = ratio > (lastRatio || 0) ? 'up' : 'down';

  // Update lastRatio only when there's a new ratio, to correctly show direction
  useEffect(() => {
    if (ratio !== 0 && ratio !== lastRatio) {
      const timeout = setTimeout(() => setLastRatio(ratio), 500);
      return () => clearTimeout(timeout);
    }
  }, [ratio, lastRatio]);

  return (
    <div className="flex min-h-screen w-full flex-col">
      <header className="sticky top-0 z-10 flex h-16 items-center gap-4 border-b bg-background/80 backdrop-blur-sm px-4 md:px-6">
        <div className="flex items-center gap-2">
          <TrendingUp className="h-6 w-6 text-primary" />
          <h1 className="text-2xl font-bold tracking-tight">Turbo Tracker</h1>
        </div>
        <div className="ml-auto">
            <Button variant="ghost" size="icon" onClick={toggleTheme}>
                <Sun className="h-[1.2rem] w-[1.2rem] rotate-0 scale-100 transition-all dark:-rotate-90 dark:scale-0" />
                <Moon className="absolute h-[1.2rem] w-[1.2rem] rotate-90 scale-0 transition-all dark:rotate-0 dark:scale-100" />
                <span className="sr-only">Toggle theme</span>
            </Button>
        </div>
      </header>
      <main className="flex-1 p-4 md:p-8 lg:p-10">
        <div className="mx-auto max-w-7xl">
          <section className="mb-8">
            <h2 className="text-xl font-semibold text-foreground/80 mb-4">Live Prices</h2>
            <div className="grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-3">
              <PriceCard 
                tokenName="Walrus"
                tokenSymbol="WAL"
                price={walPrice.price}
                direction={walPrice.direction}
                imageUrl="https://placehold.co/80x80.png"
                imageHint="walrus"
              />
              <PriceCard 
                tokenName="Sui"
                tokenSymbol="SUI"
                price={suiPrice.price}
                direction={suiPrice.direction}
                imageUrl="https://placehold.co/80x80.png"
                imageHint="water wave"
              />
              <RatioCard
                ratio={ratio}
                direction={ratioDirection}
              />
            </div>
          </section>

          <section>
            <h2 className="text-xl font-semibold text-foreground/80 mb-4">Ratio Change Log</h2>
            <Card>
              <ChangeLogTable data={ratioChanges} />
            </Card>
          </section>
        </div>
      </main>
      <footer className="border-t py-4 px-4 md:px-6">
        <p className="text-center text-sm text-muted-foreground">
          Prices are simulated for demonstration purposes.
        </p>
      </footer>
    </div>
  );
}
