import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { cn } from '@/lib/utils';
import { ArrowDown, ArrowUp } from 'lucide-react';

type RatioChange = {
  timestamp: string;
  ratio: number;
  change: number;
};

interface ChangeLogTableProps {
  data: RatioChange[];
}

export default function ChangeLogTable({ data }: ChangeLogTableProps) {
  return (
    <div className="w-full">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>Timestamp</TableHead>
            <TableHead>WAL/SUI Ratio</TableHead>
            <TableHead className="text-right">Change</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {data.length === 0 ? (
             <TableRow>
                <TableCell colSpan={3} className="text-center text-muted-foreground h-24">
                  Waiting for ratio to change...
                </TableCell>
             </TableRow>
          ) : (
            data.map((item, index) => (
            <TableRow key={index}>
              <TableCell className="font-medium">{item.timestamp}</TableCell>
              <TableCell>{item.ratio.toFixed(6)}</TableCell>
              <TableCell className={cn(
                "text-right font-medium flex justify-end items-center gap-2",
                item.change > 0 ? 'text-emerald-500' : 'text-red-500'
              )}>
                {item.change > 0 ? <ArrowUp size={16}/> : <ArrowDown size={16}/>}
                {item.change.toFixed(4)}%
              </TableCell>
            </TableRow>
          )))}
        </TableBody>
      </Table>
    </div>
  );
}
