import { useEffect, useState } from 'react';
import { ArrowUp, ArrowDown } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { cn } from '@/lib/utils';

interface RatioCardProps {
  ratio: number;
  direction: 'up' | 'down';
}

export default function RatioCard({ ratio, direction }: RatioCardProps) {
  const [displayRatio, setDisplayRatio] = useState(ratio.toFixed(6));
  const [isUpdated, setIsUpdated] = useState(false);

  useEffect(() => {
    setIsUpdated(true);
    const timer = setTimeout(() => {
      setDisplayRatio(ratio.toFixed(6));
      setIsUpdated(false);
    }, 250);
    return () => clearTimeout(timer);
  }, [ratio]);

  const ratioColor = direction === 'up' ? 'text-emerald-500' : 'text-red-500';
  const ratioIcon = direction === 'up' ? <ArrowUp className="h-4 w-4" /> : <ArrowDown className="h-4 w-4" />;

  return (
    <Card className={cn('transition-shadow duration-300 hover:shadow-lg lg:col-span-1 md:col-span-2', isUpdated && 'animate-flash')}>
       <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
        <div className="space-y-1">
            <CardTitle>WAL/SUI Ratio</CardTitle>
            <CardDescription>Live price ratio</CardDescription>
        </div>
        <div className="h-16 w-16 text-3xl font-bold flex items-center justify-center rounded-full border-2 border-muted bg-secondary">
            <span className="text-primary-foreground">W</span>
            <span className="text-muted-foreground">/</span>
            <span className="text-primary-foreground">S</span>
        </div>
      </CardHeader>
      <CardContent>
        <div className="text-4xl font-bold">{displayRatio}</div>
        <div className={cn('text-xs text-muted-foreground flex items-center gap-1 mt-1', ratioColor)}>
            {ratioIcon}
            <span>Ratio updated</span>
        </div>
      </CardContent>
    </Card>
  );
}
